
//== Typographie & text

$line-height-computed:    floor((  $line-height-base)); ; // ~20px

$pagination-border: $gris-5;
$pagination-hover-bg: $gris-5;
$pagination-active-bg: $bleu-4;

h1, .h1{
    cursor: default;
    font-family: $osp-din;
    font-size: 2.5rem;
    color: $bleu-5;

    & a{
        color:  $bleu-5;
        &:hover{
            color:  $bleu-5;
            text-decoration: none;
        }
    }

    & .sub-title{
        margin-bottom: 5px;
        color: $gris-2;
        text-transform: none;
        font-size: 0.8rem;
        @extend .texte-courant;
    }    
}

h2, .h2{
    font-family: $opensans-semi-bold;
    font-size: 1.25rem;
    &.big{
        font-size: 28px;
    }
}

h3, .h3{
    font-family: $opensans-semi-bold;
    font-size: 1.13rem;
}

h4, .h4{
    font-family: $opensans-semi-bold;
    font-size: 1rem;
}

h5, .h5{
    font-family: $opensans-semi-bold;
    font-size: 1rem;
}

h1, .h1, .application-header .title .logo, h2, .h2, h3, .h3 {
    /*margin-top: 20px;
    margin-bottom: 10px; */
}

p, .texte-courant{
    font-family: $opensans;
}




.texte-courant-semi-gras, .texte-courant-gras, strong{
    font-family: $opensans-semi-bold;
    //font-weight: 700;
}

.text-center-xs{
    @include media-breakpoint-down(sm){
        text-align: center;
    }
}


.numero{
    font-family: $opensans-semi-bold-italic;
    color: $gris-2;
    font-size: 2rem;
}

.nota{
    font-weight: bold;
    font-size: 0.69rem;
}

a{
    color: $bleu-4;
    cursor: pointer;
}

.uppercase{
    text-transform: uppercase !important;
}

.chiffre{
    color: $turquoise-2;
    font-family: $opensans-semi-bold;
    font-size: 1rem;

    &-big{
        @extend  .chiffre;
        font-size: 1.5rem;
    }

    @include media-breakpoint-down(sm){
        font-size: .8rem;
    }

}
pre{
    background-color: $gris-6;

    color: $noir;
}

abbr{
    text-decoration: none;
}

.lowercase{
    text-transform: lowercase;
}