//== Notation

.rating{

    & .star{

        font-size: 0.9rem;
        color: $gris-4;
        cursor: pointer;

        &.active{
            color: $jaune-4;
        }
    }


    &-comment{

        margin-top: 20px;
        border: 1px solid $gris-5;

        &-heading{
            padding: 10px;
            font-family: $opensans-semi-bold;
        }

        &-body{
            background-color: $gris-6;
            padding: 10px;
        }

        &-response{

            padding: 10px;

            &-title{
                font-family: $opensans-semi-bold;
            }

            &-body{
                padding-left: 20px;
            }

            & textarea[readonly]{
                width: 100%;
                border: none;
                min-height: 100px;
                outline: none;
                resize: none;
                font-family: $opensans;
                background-color: $blanc;
                transition: none;
            }
        }
    }

}