.etapes{

    &-container{

        & .card{
            position: static;
        }

        padding-bottom: 25px;

        @include media-breakpoint-up(xl){
            padding-left: 100px;
            padding-right: 100px;
        }

        @include media-breakpoint-up(lg){
            padding-left: 60px;
            padding-right: 60px;
        }

        @include media-breakpoint-down(sm){
            padding-left: 10px;
            padding-right: 10px;
        }

        & .etape-footer{
            min-height: 65px;
            background-color: $gris-6;
            padding: 15px;
            border-radius: 5px;
            text-align: right;
            @include media-breakpoint-down(sm){
                text-align: center;
            }
        }


    }

    margin-right: auto;
    margin-left: auto;
    width: 85%;

    @include media-breakpoint-down(sm){
        width: 60%!important;
    }

    & li{

        & .numero{
            display: none;
            width: 45px;
            height: 45px;
        
            margin-right: auto;
            margin-left: auto;
        
            top: -25px;
            position: relative;
        
            border-radius: 40px;
            font-family: $opensans-semi-bold-italic;
            font-size: 2rem;
            text-align: center;
            background: $gris-5;
        }

        text-align: center;
        width: 33.333333%;

        & a{
            cursor: default;
            height: 60px;
            border-bottom: none;
            &:hover{
                background-color: $gris-6;
            }
        }

        @include media-breakpoint-down(sm){
            display: none;
            margin: 0;
        }

        &.inactive{
            & a{
                color: $gris-3  !important;
            }
        }

        &.active {

            display: block;

            @include media-breakpoint-down(sm){
                width: 100%;
            }

            & .numero{

                line-height: 35px;

                @include media-breakpoint-down(sm){
                    width: 50px;
                    height: 50px;
                    line-height: 40px;
                }

                color: $blanc;
                background-color: $bleu-5;
                border: 5px solid $gris-5;
                font-size: 1.3rem;
                display: block;
                top:-10px;
                margin-bottom: -40px;
            }
        }
    }
}