.main-menu{

    height: 200%;
    width: 0;
    position: fixed;
    z-index: 0;
    top: 0;
    left: 0;

    background-color: $bleu-5;
    overflow-x: hidden;
    opacity: 0;

    & h2{
        color: $blanc;
        margin-top: 10px;
        margin-left: 10px;
    }

    & .lien-amelipro{
        margin: 10px;
        background: url("#{$image-url}/btn_home.svg") no-repeat;
        height: 30px;
        &:hover{
            background-image: url("#{$image-url}/btn_home_fb.svg");
            text-decoration: none;
        }
    }

    &.open{
        width: 300px;
        opacity: 1;
        z-index: 1042;
    }

    & .btn-menu{
        color: $blanc;
        &.glyphicon{
            font-size: 1.2rem;
       }
    }

    & a {
        color: $blanc;
        padding: 10px;
        padding-left: 30px;
        display: block;

        &:hover{
            color: $blanc;
            text-decoration: none;
        }
    }

    & ul {
        margin: 0;
        padding:0;
        list-style: none;

        & ul{
            margin-left: 15px;
        }
    }

    & li{
        padding: 0;
        margin-top: 5px;
        margin-bottom:  5px;

        &:hover{
            background-color: $bleu-4;
        }

        &.divider{
            border-top: 1px solid $blanc;
        }

        &.active{
            background-color: $bleu-3;
        }
    }

}

.menu-ancre{
    
    margin-left: auto;
    margin-right: auto;
    display: table;

    & li {
        float: left;
        margin-left: 10px;
        margin-right: 10px;
        text-align: center;

    }

    & a{
        color: $noir;

        &:hover{
            color: $bleu-4;
        }

        &.active{
          color: $bleu-4;
        }
 
    }

}

.desktop{

    &-menu{

        & ul{

            background-color: $gris-7;
            display: table;
            width: 100%;
            padding: 0;
            margin: 0;

            & li{
                list-style: none;
                padding: 10px;
                float: left;
                margin: 0;
            }

            & li.active{
                background-color: $gris-4;
                & a{
                    color: $gris-1;
                }
            }

            & a{
                text-transform: uppercase;
                color: $gris-2;
                &:hover{
                    text-decoration: none;
                    color: $gris-1;
                }
            }
        }
    }
}

.mobile{
    &-menu{

        position: absolute;
        z-index: 1000;
        top: 0;
        width: 100%;
        opacity: 0.9;
        min-height: 700px;
        background-color: $gris-1;

        & ul{
            margin-top: 5px;
            padding: 30px 10px 0 30px;
            min-height: 700px;
            width: 40%;
            background-color: $bleu-4;
            opacity: 1;
        }

        & li{
            margin-bottom: 5px;
        }

        & a {
            color: $blanc;
            font-size: 1rem;
        }
    }
}

// Bouton menu

.btn-menu{

    color: $gris-1;
    background-color: transparent;
    border: none;
    //margin-left: -15px;

    & .glyphicon{
        font-size: 20px;
        font-weight: normal;
    }

}

//== Menu de service amelipro

.menu-services{

    & .popover{

        width: 600px;
        max-width: 600px;

        @include media-breakpoint-down(xs){
            width: 320px;
            max-width: 320px;
        }

        & .popover-content{
            font-family: $opensans;
            font-size: .65rem;
            font-weight: normal;
            padding: 0;
        }
    }



    & .colonne-1{
        background-color: $bleu-8;
        & .service{
            float: none;
            width: 100%;
        }
    }


    & .service{

        opacity: 1;
        transition: opacity .5s ease-out;

        margin-top: 15px;
        padding-top: 5px;
        width: 25%;

        min-height: 100px;

        @include media-breakpoint-down(xs){
            min-height: 70px;
        }

        display: block;
        float: left;
        text-align: center;

        &:hover{
            opacity: 0.8;
        }

        & .service-description{
            @include media-breakpoint-down(xs){
                display: none;
            }
            margin-top: 5px;
        }
    }

}