header{
    position: relative;
    width: 100%;
}

.top-header-logo {
    width: 136px;
    height: 52px;
    background: url("#{$image-url}/logo_am_entete.svg") no-repeat center;
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -68px;
    z-index: 500;

    & a{
        width: 100%;
        height: 52px;
        display: block;
    }
}

.top-header-bg{

    height: 30px;

    width: 100%;
    background-color: $bleu-2;
    position: relative;

    & a{
        color: $blanc;
    }

    & .lien-espacepro, & .lien-amelipro{

        background: url("#{$image-url}/btn_home.svg") no-repeat center;
        background-size: cover;
    
        display: block;
        width: 130px;
        height: 30px;

        &:hover{
            background-image: url("#{$image-url}/btn_home_fb.svg");
            text-decoration: none;
        }

        @include media-breakpoint-down(sm){
            background-image: url("../img/btn_home_mobile.svg");   
            border-radius: 100%;      
            width: 34px;
            height: 34px;  
            position: absolute;
            top: 11px;
            left: 11px;  

            &:hover{
                background-image: url("#{$image-url}/btn_home_mobile_fb.svg");
            }
    
        }

    }
}

.application-header{

    height: 70px;

    h1{
        @include media-breakpoint-down(sm){
            margin-top: 35px;
            font-size: 1.75rem;
            margin-left: 40px;
        }

        @include media-breakpoint-up(sm){
            margin-left: 40px;
        }

    }

    @include media-breakpoint-down(sm){
        button.pictogramme-svg-user{
        position: absolute;
        top: -44px;
        right: 5px;          
    }
}



}

//== Sous header des applications
.application-sub-header{
    padding-top: 25px;
    background-color: $gris-6;
    border-top: 1px solid $gris-4;
    border-bottom: 3px solid $gris-4;

    &-title{
        @extend .texte-courant-gras;
        @extend .texte-bleu-4;
        padding-top: 10px;
    }

    & .close{
        font-size: 14px;
    }


}
.sticky-header{

    position: fixed;
    z-index: 1040;


    @include media-breakpoint-down(sm){
        button.pictogramme-svg-user{
        position: static;
    }}

    & .top-header-bg{
        display: none;
    }

    .application-sub-header{
        padding-top: 10px;
    }

    & .logo-amelipro{
        height: 36px;
        display: block;
        transition: 0.2s;
        @include media-breakpoint-down(sm){
            display: none;
        }
    }

    & h1{
        transition: 0.2s;


    }

    & .texte-icone{
        display: none;
    }

    & .application-header .navigation{

        top: 15px;

        @include media-breakpoint-down(sm){
            top: 10px;
        }

        & .pictogramme-svg-menu{
            @include media-breakpoint-down(sm){
                margin-top: 10px;
            }
        }

        & .pictogramme-svg-user{
            @include media-breakpoint-down(sm){
                margin-top: 5px;
            }
        }

        & .pictogramme-svg-aide{
            @include media-breakpoint-down(sm){
                margin-top: 10px;
                width: 32px;
                height:32px;
            }
        }     
    }

    & .info-patient{
        display: none;
    }

    & .fleche-droite{
        display: none;
    }

    .pictogramme-svg-home-mobile{
        @include media-breakpoint-down(sm){
            display: none;
        }
    }

   
    & .logo-backoffice{
        display: block !important;
    }     
    
 
}
.application-header{
    background-color: $blanc;
    & .navigation{
        //@extend .container;
        @include media-breakpoint-down(sm){
            width: inherit;
        }
    }
}