.doc-icone-bloc{
    text-align: center;
    float: left;
    height: 220px;
    padding: 10px;
    width: 200px;
    border: 2px solid $gris-6;
    margin-right: 15px;

    &:hover{
        border: 2px solid $gris-1;
    }

    &.disabled {
        opacity: 0.5;
    }

    &-description{
        min-height: 50px;
    }
}

//== Class specifiques interface de documentation

.doc{

    &-typographie td{
        vertical-align: middle !important;
    }

    &-body{
        background-color: $gris-6;
    }

    &-entete{
        height: 50px;
        border-right: 1px solid #000;
        padding-right: 5px;
        margin-bottom: 5px;
    }

    &-corps{
        height: 450px;
        border-right: 1px solid #000;
        padding-right: 5px;
        margin-bottom: 5px;
    }

    &-pied{
        height: 180px;
        border-right: 1px solid #000;
        padding-right: 5px;
        margin-bottom: 5px;
    }

    &-code-source{

        border: 1px solid $gris-5;
        background: $gris-6;
        margin-bottom: 10px;
        margin-top: 10px;
        width: 100%;
        min-height: 100px;

    }

    &-header{

        /*text-align: center;*/
        margin-left: -15px;
        margin-right: -15px;
        background-color: $bleu-1;
        color: $blanc;
        top: 0;
        z-index: 1000;
        opacity: 0.8;
        position: sticky;

        & h1{
            color: $blanc;
        }

    }

    &-header-small{

        & .doc-btn-menu{
            margin-top: 5px;
            margin-bottom: 5px;
            display: block;
            font-size: 2rem;
            transition: 0.5s;
        }

        & h1{
            margin-top: 5px;
            margin-bottom: 5px;
            transition: 0.5s;
        }
    }


    &-btn-menu{
        color: $blanc;
        margin-top: 20px;
        margin-bottom: 10px;
        display: block;
        font-size: 2rem;
        text-align: center;
    }

    &-menu{

        overflow: hidden;
        height: 400px;

        &  nav{
            position: fixed;
        }

        border-right: 1px solid $bleu-5;
        display: none;
        & li {
            margin-bottom: 4px;
            & a{
                color: $bleu-9;
            }
            &.active a{
                text-decoration: underline;
                @extend .texte-courant-gras;
            }
        }
    }

    &-colors{
        text-align: center;
        border: 1px solid $gris-3;
        padding: 10px;
        width: 180px;
        float: left;
        margin-right: 5px;
        margin-top: 5px;
    }

    &-dark-bg{
        padding-top: 5px;
        padding-bottom: 10px;
    }

    &-section{
        padding-top: 100px;
    }
}