.error{

    color: $alert-danger-text;

    & input.form-control{
        border: 1px solid $alert-danger-text;
    }

    &.radio{
        & label {color: $alert-danger-text;}
    }

    & label{
        color: $gris-1 !important;
    }
}

.form-control{

    color: $gris-1;
    background-color: $gris-7 !important;
    box-shadow: none;
    border: 1px solid $gris-3;
    height: 36px;

    @include media-breakpoint-down(sm){
        height: 42px;
    }

    font-family: $opensans ;
    font-size: 0.81rem;

    &.small-input{
        width: 60px;
        display: inline;
    }

    &[disabled], &.disabled{
        color: #808080;
        background-color: $gris-5 !important;
        border: 1px solid $gris-3;
    }
}

.input-group{

    & input{
        border-right: none;
    }

    & .input-group-addon{

        &-search{
            background-color: $bleu-4;
            color: $blanc;
        }

        &-not-ok{
            background-color: $gris-7 !important;
            color:  $gris-3;
        }

        &-ok{
            background-color: $gris-7 !important;
            color:  $bleu-4;
        }
    }
}

.bg-gris-6{
    & .form-control{
        background-color: $blanc !important;
    }

    & .bootstrap-select{
        & .btn{
            @include media-breakpoint-down(sm){
                height: 42px !important;
            }

            &-default{
                background-color: $blanc !important;
            }
        }
    }

}

.easy-autocomplete input{
    box-shadow: none;
    color: $gris-1;
}

.required-input{
    font-size: .82rem;
    margin-top: 8px;
    margin-left: -15px;
    padding-top: 5px;
    display: block;
    &:before {
        content: "*";
    }
}


.control-label, label {
    padding-right: 0;
    font-weight: normal;
    font-family: $opensans;    
}

.dark-bg{


    & a{
        color: $blanc !important;
    }

    & .form-control{
        background-color: $blanc !important;
    }

    & .form-control[disabled],  & .form-control[readonly], & fieldset[disabled] .form-control{
        background-color: $gris-5 ;
    }

    & .datepicker[disabled]{
        background-color: $blanc !important;
    }

    & .checkbox .styled-label, & .radio .styled-label{

        color: $gris-1;
        background-color: $blanc;

        &.active{
            color: $blanc;
            background-color: $bleu-5;
        }
    }

    & .toogle-btn {
        background-color: $blanc;
    }

    & .bootstrap-select{
        & .btn{
            @include media-breakpoint-down(sm){
                height: 42px !important;
            }

            &-default{
                background-color: $blanc !important;
            }
        }
    }
}

textarea{
    resize: none;
}