/* Pictogramme glyphicon de couleur */


.pictogramme-avec-fleche{
    @include media-breakpoint-up(lg){
        margin-right: 20px !important;
    }
}


@mixin pictogramme-svg( $nom-picto, $image-svg, $image-svg-hover, $bg-hover ){

    @if $image-svg-hover != '' {
        a:hover &-svg-#{$nom-picto}{
            background-image: url("#{$image-url}/#{$image-svg-hover}.svg");
            @if $bg-hover != '' {
                background-color: $bg-hover;
            }
        }
    }


    &-svg-#{$nom-picto}{

        @if  $nom-picto == 'menu' OR $nom-picto == 'panier-blanc' OR  $nom-picto == 'aide' OR $nom-picto == 'erreur' OR $nom-picto == 'attention' OR $nom-picto == 'information' OR $nom-picto == 'astuce'{
            width: 24px;
            height: 24px;
            line-height: 24px;    
        }
    
        position: relative;
        
        & .fleche-droite{
            background-image: url("#{$image-url}/picto_bt_entete.svg");
            background-repeat: no-repeat;
            position: absolute;
            right: -10px;
            top: 16px;
            display: none;
            height: 5px;
            width: 8px;

            @include media-breakpoint-up(lg){
                display: block;
            }
        }

        @extend  .pictogramme;
        @include before-content();
        border: none;
        background-image: url("#{$image-url}/#{$image-svg}.svg");

        @if $image-svg-hover != '' {
            &:hover, &.hover{
                background-image: url("#{$image-url}/#{$image-svg-hover}.svg");
                @if $bg-hover != '' {
                    background-color: $bg-hover;
                }
            }
        }

        &.small{
            width: 15px;
            height: 15px;
            line-height: 15px;
        }

        &.large{
            width: 48px;
            height: 48px;
            line-height: 48px;
        }

        &.x-large{
            width: 60px;
            height: 60px;
            line-height: 60px;
        }

        &.big{
            width: 82px;
            height: 82px;
            line-height: 82px;
        }

        &.x-big{
            width: 108px;
            height: 108px;
            line-height: 108px;
        }

    }

}
