/* Couleur de texte  & couleur de BG*/

$couleurs: (
    noir  : $noir ,
    blanc : $blanc,
    gris-1 : $gris-1,
    gris-2 : $gris-2,
    gris-3 : $gris-3,
    gris-4 : $gris-4,
    gris-5 : $gris-5,
    gris-6 : $gris-6,
    gris-7 : $gris-7,
    bleu-1 : $bleu-1,
    bleu-2 : $bleu-2,
    bleu-3 : $bleu-3,
    bleu-4 : $bleu-4,
    bleu-5 : $bleu-5,
    bleu-6 : $bleu-6,
    bleu-7 : $bleu-7,
    bleu-8 : $bleu-8,
    bleu-9: $bleu-9,
    bleu-10: $bleu-10,
    turquoise-1 : $turquoise-1,
    turquoise-2 : $turquoise-2,
    turquoise-3 : $turquoise-3,
    turquoise-4 : $turquoise-4,
    turquoise-5 : $turquoise-5,
    turquoise-6 : $turquoise-6,
    vert-1 : $vert-1,
    vert-2 : $vert-2,
    vert-3 : $vert-3,
    vert-4 : $vert-4,
    vert-5 : $vert-5,
    jaune-1 : $jaune-1,
    jaune-2 : $jaune-2,
    jaune-3 : $jaune-3,
    jaune-4 : $jaune-4,
    jaune-5 : $jaune-5,
    ocre-1 : $ocre-1,
    ocre-2 : $ocre-2,
    ocre-3 : $ocre-3,
    ocre-4 : $ocre-4,
    ocre-5 : $ocre-5,
    rouge-1 : $rouge-1,
    rouge-2 : $rouge-2,
    rouge-3 : $rouge-3,
    rouge-4 : $rouge-4,
    rouge-5 : $rouge-5,
    rouge-6	: $rouge-6
);

@mixin generate-color-class(){

	@each $couleur , $value in $couleurs {

		.texte-#{$couleur}{
			color:  #{$value} !important;
		}

		.bg-#{$couleur}{
			background-color:  #{$value} !important;
		}


	}

}

@include generate-color-class() ;
