//== Badges

.badge{

    border-radius: 100px;

    font-family: $opensans-semi-bold;
    font-size: 0.75rem;
    padding: 4px 7px;
    background-color: $blanc;
    color: $bleu-3;
    
    &-bleu{
        background-color: $bleu-5;
        color: $blanc;
    }
    &-turquoise{
        background-color: $turquoise-2;
        color: $blanc;
    }
    &-vert{
        background-color: $vert-1;
        color: $blanc;
    }
    &-jaune{
        background-color: $jaune-1;
        color: $blanc;
    }
    &-rouge{
        background-color: $rouge-1;
        color: $blanc;
    }

    &-text{
        border-radius: 0px 10px 10px 0px;
    }
}