//== Popover
.popover {
    border-radius: 6px;
    margin-right: 5px;
    width: 280px;

    &-title {
        background: transparent;
        border: none;
    }

    &-input {
        & .popover {
            max-width: 100% !important;
            width: 92% !important;
            left: 0px !important;
            margin-top: 0;

            & .arrow {
                display: none;
            }
        }
    }
}
