.nav-tabs{

    & .nav-item{

        & .nav-link{
            background-color: $gris-6;
            color: $gris-2;
            margin-right: 6px;
            border: none;
            border-radius: 0;
            border-bottom: 6px solid $gris-5;
        }

        & .active{

            color: $blanc;
            background-color: $bleu-5;
            border: none;
            border-bottom: 6px solid $bleu-5 !important;
            border-radius: 0;
        
        }

        & .disabled{
            color: $gris-3;
            background-color: $gris-7;
        }

    }
}