//== Widgets

.widget{
    padding: 5px;
    border: 1px solid $gris-5;
    min-height:160px;

    & h3, & .h3{
        margin-top: 10px;
    }
}

.panel{
   & .widget{
    padding: 10px;
    min-height:160px;

        & h3, & .h3{
            margin-top: 0;
            font-size: 17px;
        }
    }
}