//== Cercles colores

.cercle{

    width:10px;
    height: 10px;
    border-radius: 100%;
    display: inline-block;

    &-bleu{
        @extend  .cercle;
        background-color: $bleu-5;
    }
    &-turquoise{
        @extend  .cercle;
        background-color: $turquoise-4;
    }
    &-ocre{
        @extend  .cercle;
        background-color: $ocre-4;
    }
    &-jaune{
        @extend  .cercle;
        background-color: $jaune-4;
    }
    &-rouge{
        @extend  .cercle;
        background-color: $rouge-3;
    }

    &-separateur{
        @extend  .cercle;
        width:5px;
        height: 5px;
        background-color: $bleu-5;
    }

    &.big{
        width: 40px;
        height: 40px;
    }
}