html {
    font-size: 100%;
}

body{

    @include media-breakpoint-up(xl){
        overflow-y: scroll;
    }

    font-family: $opensans;
    font-size: $body-font-size-base;
    color: $gris-1;
    background-color: $gris-5;

}


//== STRUCTURE GNRALE DES PAGES

.global-container{

    @extend  .container;
    overflow: hidden;
    position: relative;
    padding: 0;
    width: 100%;
    max-width: 100%;
}

main{
    min-height:530px;
    padding: 25px 15px;
    background-color: $gris-5;

    @include media-breakpoint-down(sm){
        padding: 10px 0;
    }

    @extend .container;

    &.sticky-header-enabled{
        margin-top: 132px;
    }
}

.main-large{
    width: 100% !important;
    max-width: 100% !important;
    padding-top: 0px;
}

.main-doc{
	min-height:800px;
}