//== Spacers


.big-spacer{
    margin-top: ($grid-gutter-width * 2);
}

.spacer{
    margin-top: $grid-gutter-width;
}

.medium-spacer{
    margin-top: ($grid-gutter-width / 2);
}

.small-spacer{
    margin-top: ($grid-gutter-width / 3);
}

.very-small-spacer{
    margin-top: 5px;
}

.margin {

    &-right{
        &-5{ margin-right: 5px; }
    }

    &-right{
        &-10{ margin-right: 10px; }
    }

    &-right{
        &-15{ margin-right: 15px; }
    }

    &-right{
        &-30{ margin-right: 30px; }
    }

    &-right{
        &-40{ margin-right: 40px; }
    }

    &-left{
        &-5{ margin-left: 5px; }
    }

    &-left{
        &-10{ margin-left: 10px; }
    }

    &-left{
        &-15{ margin-left: 15px; }
    }

    &-left{
        &-30{ margin-left: 30px; }
    }

    &-left{
        &-40{ margin-left: 40px; }
    }
}

.padding {

    &-5{
        padding: 5px;
    }

    &-10{
        padding: 10px !important;
    }

    &-15{
        padding: 15px;
    }

    &-20{
        padding: 20px;
    }

    &-right{
        &-5{ padding-right: 5px; }
    }

    &-right{
        &-10{ padding-right: 10px; }
    }

    &-right{
        &-15{
            padding-right: 15px; }
    }

    &-left{
        &-5{ padding-left: 5px; }
    }

    &-left{
        &-10{ padding-left: 10px; }
    }

    &-left{
        &-15{ padding-left: 15px; }
    }
}

.no-padding {

    padding: 0;


    &-left{
        @include media-breakpoint-up(lg){
            padding-left: 0 !important;
        }
    }
    &-right{
        @include media-breakpoint-up(lg){
            padding-right: 0 !important;
        }
    }

    &-bottom{
        @include media-breakpoint-up(lg){
            padding-bottom: 0 !important;
        }
    }


}

.no-margin {

    margin: 0  !important;

    &-left{
        @include media-breakpoint-up(lg){
            margin-left: 0 !important;
        }
    }
    &-right{
        @include media-breakpoint-up(lg){
            margin-right: 0 !important;
        }
    }

    &-bottom{
        @include media-breakpoint-up(lg){
            margin-bottom: 0 !important;
        }
    }


}