// Alert

.alert{

    padding-left: 10px;
    background-color: transparent;
    border-color: $gris-5;

    &-danger{
        color: $danger;
    }

    &-warning{
        color: $warning;
    }

    &-info{
        color: $info;
    }

    &-success{
        color: $success;
    }

    &-dismissable .close, &-dismissible .close{
        top: 0;
        color: $gris-1;
    }

    & p{
        margin-top: -23px;
        margin-left: 35px;
        margin-bottom: 0;
    }

    & ul{
        margin-top: -20px;
        margin-left: 10px;
        margin-bottom: 20px;
    }



}

.message-multiligne{

    & p{
        margin-top: -25px;
        margin-left: 35px;
    }
}

//== Notification

.notification{

    position: relative;
    margin-right: 10px;

    & .badge{
        position: absolute;
        right: -12px;
        top: -10px;
    }

    &-message{

        & h3{
            font-family: $opensans;
            color: $bleu-4;
            margin-top: 0;
            margin-bottom: 10px;
        }

        & small{
            color: $gris-1;
        }

        & p{
            padding: 5px 5px 5px 45px;
            margin-bottom: 5px;
            background: $bleu-8 url("#{$image-url}/picto_notification_ouvert.svg") no-repeat 10px center;
        }

    }

}