/*Fontes externes*/

@font-face {
    font-family: 'OPENSANS';
    src: url('#{$font-url}/opensans-regular-webfont.eot');
    src: url('#{$font-url}/opensans-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$font-url}/opensans-regular-webfont.woff') format('woff'),
         url('#{$font-url}/opensans-regular-webfont.ttf') format('truetype'),
         url('#{$font-url}/opensans-regular-webfont.svg#open_sansregular') format('svg');
}
