/* Switch toggle **/

div.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 24px;

    input {
        display:none;
    }    

    & small{
        position: absolute;
        right: -55px;    
    }


}

label.switch{
    max-width: 60px;
}

.padding-switch-top {
    padding-top: 13px;
}
.toogle-btn {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $gris-3;
    -webkit-transition: .4s;
    transition: .4s;

    & small::before{
        content: 'Inactive';
    }

}
.toogle-btn:before {
    position: absolute;
    top:-4px;
    content: "";
    height: 29px;
    width: 29px;
    left: 0px;
    bottom: 4px;
    background-color: $gris-2;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .toogle-btn:before {
    background-color: $turquoise-3;


}

input:checked + .toogle-btn {
    background-color: $bleu-7;

    & small::before{
        content: 'Active';
    }

}

input:disabled + .toogle-btn:before {
    opacity: 0.6;
}

input:disabled + .toogle-btn {
    opacity: 0.6;
}

input:focus + .toogle-btn {
    box-shadow: 0 0 3px #2196F3;
}

input:checked + .toogle-btn:before {
    -webkit-transform: translateX(36px);
    -ms-transform: translateX(36px);
    transform: translateX(36px);
}
.toogle-btn.round {
    border-radius: 20px;
    height: 20px;
}

.toogle-btn.round:before {
    border-radius: 50%;
}