//== Pop-in modal

.modal{

    &-title{
        font-size: 0.94rem;
        font-family: $opensans;
        font-style: italic;
        color: $gris-1;
    }

    &-header{
        padding: 14px;
    }

    &-footer{
        display: block;
        padding: 8px 15px;
        background-color: $gris-6;
    }
}