// Table

.table{

    @mixin table-border-blanc(){
        &>thead>tr>th,
        &>tbody>tr>td,
        &>tfoot>tr>td{
            border-color: $blanc;
        }
    }

    thead th{

        text-transform: uppercase;

        & .glyphicon{
            font-size: .88rem ;
            color:  $bleu-4;
        }

        &.filter{

            cursor: pointer;

            &:hover{
                background-color: $gris-5;
            }

            & .dropdown-menu{
                min-width: 255px;
                padding-left: 10px;
                top: 24px;
                border-radius: 0;
                border: 1px solid $gris-5;
                left: -8px;
                -webkit-box-shadow: none;
                box-shadow: none;
            }
        }


    }

    &-gris{
        background-color: $gris-5;
        @include table-border-blanc();
    }
    &-bleu{
        background-color: $bleu-8;
        @include table-border-blanc();
    }

    &-thead-gris{
        color: $blanc;
        background-color: $gris-2;
    }

    &-thead-bleu{
        color: $blanc;
        background-color: $bleu-4;
    }


    & a.trie .glyphicon{
        color: $gris-3;
    }

    &-crud{
        border-bottom: 1px solid $table-border-color;
    }

}