.checkbox, .radio{
	& label{
		margin-right: 20px;
		&.active{
			font-family: $opensans-semi-bold ;
		}
	}
}

 
.checkbox .styled-label, .radio .styled-label{

	min-width: 180px;
	text-align: center;
    padding: 7px 30px;
    border-radius: 20px;
    background-color: $gris-5;
    font-family: $opensans-semi-bold;
    margin-right: 5px;

    & input{
        opacity: 0;

    }

    &.active{
        color: $blanc;
        background: $bleu-5 url("#{$image-url}/picto_coche_select_btn_important.svg") no-repeat left;
    }
}