//== Formulaire

.file{

    &-uploader{

        @extend .pictogramme-svg-telechargement;

        width: 42px;
        height: 42px;
        line-height: 42px;
        font-weight: normal;
        font-size: 1rem;

        position: relative;

        &.active{
            background: url("#{$image-url}/loader-telechargement.gif") no-repeat center;
            border: 1px solid $gris-6;
        }

        & input{
            cursor: pointer;
            position: absolute;
            top: 0;
            height: 42px;
            padding-left: 42px;
            opacity: 0;
            overflow: hidden;
            width: 42px;
        }
    }


    &-uploader-btn{

        /*@include icone-cercle(); */

        width: 42px;
        height: 42px;
        line-height: 42px;
        font-weight: normal;
        background-color: $blanc;
        color: $bleu-3;
        border: 1px solid $gris-3;
        border-radius: 50px;
        padding: 7px 15px;

        position: relative;



        &.active{
            background-color: $blanc;
            background: url("#{$image-url}/loader-telechargement.gif") no-repeat center;
            border: 1px solid $gris-4;
            color: $gris-4;
        }

        & input{
            cursor: pointer;
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            overflow: hidden;
            height: 38px;
            width: 100%;
        }
    }

    &-name{
        padding-top: 3px;
        padding-bottom: 3px;
        padding-left: 20px;
        border-radius: 20px;
        margin-right: 5px;
        margin-top: 10px;
        display: table;


        background-color: $bleu-8;
        font-family: $opensans-semi-bold;

        & .icone{
            margin-right: 0;
            margin-left: 10px;
        }
    }   
}

//== Formulaire d'identification des patients

.form-identification{

    position: relative;
    margin-right: auto;
    margin-left: auto;
    padding: 10px;
    margin-bottom: 30px;

    @extend .widget;
    @extend .text-center;

    background-color: $blanc;
    min-height: 150px;

    &:hover{
        background-color: $gris-5;

        & .action-button{
            display: block;
        }
    }

    & .action-button{
        position: absolute;
        bottom: -15px;
        left: 35%;
        display: none;
        border-color: $bleu-3;
    }

    &-title{
        @extend .texte-courant-semi-gras;
    }

    &-sepateur{
        margin-top: 50px;
        margin-bottom: 50px;

        @extend .icone;
        @extend .x-large;
        @extend .bouton-aide-bleu;
        @extend .active;
    }

}
 