.design-systeme{
    
    & .btn-retour-haut{
        top: -5px !important;
    }

    & .regle-usage{
        padding: 10px 20px 10px 20px;
        background: $gris-6;
    }

    background-color: #F9F9F9;
    & .global-container{
        background-color: #F9F9F9;
    }

    &-contenu{
       margin-bottom: 50px;
       margin-left : 350px;
       & .page{
           margin-top: 50px;
       }

       & h2, h3{
           &.spacer{
           margin-top: 50px;
           }
       }
    }

    &-menu{
        
        position: fixed;
        height: 1600px;
        display: block;
        background-color: $bleu-2;

        & ul { 

            padding: 0;
            margin: 0;

            & li {

                & a{
                    text-decoration: none;
                    padding-left: 30px;
                    padding-top: 5px;
                    padding-bottom: 5px;
                    width: 100%;
                    display: block;

                }

                & ul{

                    & li{
                        
                        & a{

                            font-size: 15px;
                            font-family: $opensans;

                            &:hover, &.active{
                                background: #27C9FD;
                            }
                        }
                    }
                }

                list-style: none;
                margin-top: 10px;

                & a{
                    font-size: 16px;
                    font-family: $opensans-bold;
                    color: $blanc;
                    &:hover{
                        color: $blanc;
                    }
                }
            }
        }

        & .menu-gauche{
            padding-top: 30px;
            width: 50px;
            float: left;
        }

        & .menu-droite{
            padding-top: 30px;
            width: 300px;
            float: right;
            height: 1500px;

            & .menu-niveau-2{
                display: none;
            }
        }

        & a.btn-menu{

            &:hover{
                text-decoration: none;
            }

            & .glyphicon{
            color: $blanc;
            margin-top: 0px;
            margin-bottom: 10px;
            display: block;
            font-size: 1.5rem;
            text-align: center;
            }
        }        
    }


    & ul.liste-styled{

        & li {
            margin-top: 15px;
            list-style: url(#{$image-url}/picto_fleche_droite.svg) ;
        }

        &-tiret{

            @extend .list-unstyled;
            & li{
                list-style: none;
            }

            & li:before{
                content: '- ';   
            }    

        }        
    }    

    &-page-index{
        & h2{
            font-size: 47px;
            color: #01B9F5;
        }
    }

    & .a-propos{
        & h2{
            font-family: $opensans-light;
            font-size: 26px;
            color: $bleu-5;
        }
    }

    &-introduction{
        font-size: 30px;
        color: #01B9F5;
    }

    & .bg-a-propos-image-1{
        background-image: url(#{$image-url}/img_design_system/a-propos-image-1.svg) ;   
        background-position-x: 95%;
        background-position-y: 95%;
        background-repeat: no-repeat;
        background-size: 40%;

        @include media-breakpoint-down(sm){
            background: none;
        }
    }

    & .panel-body, & .panel-heading{
        padding-left: 30px;
        padding-right: 30px;
    }

    .liste-capsules{

        & div{
            position: relative;
            border-radius: 5px;
            border: 1px solid #F4F4F4;
            padding: 10px;
            margin-bottom: 15px;
            width: 100%;

            & .glyphicon{
                position: absolute;
                top : 10px;
                right: -20px;
            }
        }

        &.correct{
            & .glyphicon{ color: $vert-3; }
            & div{border: 1px solid $vert-5;}
        }

        &.incorrect{
            & .glyphicon{ color: $rouge-3; }
            & div{border: 1px solid $rouge-4;}
        }
       
    }

    .code-source{

        .panel-title{
            color: $bleu-5;
        }

        font-family: Courier New, Courier, monospace !important;
        font-size: 12px;
        & .comment{
            color: $gris-2;
            font-style: italic;
        }
    }

    .carte-couleur{

        font-size: 10px;
        border: 1px solid #E2E2E2;
        
        &-header{
            min-height: 100px;
            padding: 10px;
            border-bottom: 1px solid #E2E2E2;
        }
        &-footer{
            min-height: 120px;
            background: $gris-7;
            padding: 10px;
        }

        &-libelle{
            color: #868686;
            font-size: 9px;
        }
        
        &-nom-couleur{
            font-size: 14px;
        }


        &-palette{
            @extend .carte-couleur;
            padding: 5px;
            background: #F5F5F5;
            font-size: 12px;

            & .carte-couleur-nom-couleur , & .carte-couleur-libelle{
                font-size: 12px;
            }

            & .carte-couleur-carre{
                border: 1px solid #E2E2E2;
                height: 40px;
                width: 40px;
            }
        }
    }

    .hover{
        & .btn-primary, & .btn-default{
            background-color: $bleu-3;
            border-color: $bleu-3;
            color: $blanc;
        }

        & a{
            color: $bleu-4;
            text-decoration: underline;
        }
    }


    // Exemples de codes sources - styles spcifique prism 

    .code-toolbar {
        //width: 450px;
       margin-top: 30px;
    }

    pre {
        border-radius: 5px;
        border: none;
    }


    div.code-toolbar > .toolbar{
        top: 0;
        right: 15px;
    }    

    div.code-toolbar>.toolbar>div.toolbar-item {
        position: relative;
        overflow: visible;
    }

    div.code-toolbar>.toolbar>div.toolbar-item a {
        font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
        padding: 5px 15px 0;
        position: absolute;
        top: -15px;
        right: -40px;
        transform: translate(-10px, -90%);
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        color: white;
        height: 25px;
        background: $gris-1 ;
        cursor: pointer;
        transition: all 200ms ease;
    }

    div.code-toolbar>.toolbar>div.toolbar-item:hover a {
        transform: translate(-10px, -100%);
    }

    div.code-toolbar>.toolbar>div.toolbar-item:hover a:hover {
        color: gray;
    }    

    :not(pre) > code[class*=language-], pre[class*=language-]{
        background: $gris-1 ;
    }    

}