//== Icones

@mixin icone-cercle(){

    display: inline-block;
    text-align: center;
    /*vertical-align: middle;*/
    color: $gris-1;
    font-family: $opensans-semi-bold;
    background-color: $blanc;
    border: 1px solid $gris-1;
    border-radius: 100%;

    & .glyphicon{
        line-height: none;
    }

}