// SLIDER

.slider{

    &-track{
        background-color: $gris-5;
        box-shadow: none;
        background-image: none;
    }

    &-handle{
        background-image: none;
    }

    &-selection{
        background-color: #D1EAFF;
        background-image: none;
        box-shadow: none;
        background-image: none;
    }

    & .tooltip.top .tooltip-arrow{
        border-top-color: $gris-4;
    }

    & .tooltip-inner{
        background-color: $blanc;
        color: $noir;
        border: 1px solid $gris-4;
    }
}