//== Login box

.login-box{

    /*width: 400px;*/
    margin-top: 45px;
    padding: 0;

    &-title{

        position: relative;

        background-color: $bleu-5;
        color: $blanc;
        font-size: 1rem;
        text-transform: uppercase;
        font-family: $opensans;
        text-align: center;

        margin-right: 25px;
        margin-left: 25px;

        @media ( max-width: $screen-phone){
            margin:0;
        }

        &:after{
            content: '';
            width: 1rem;
            height: 1rem;
            transform: rotate(45deg);
            background-color: $bleu-5;
            position: absolute;
            bottom: -8px;
            left: 49%;
            z-index: 3;
        }

        & h1{
            margin: 0;
            line-height: 40px;
            font-family: $opensans;
            font-size: 1rem;
            color: $blanc;
        }
    }

    &-form{

        padding-top: 25px;
        margin-bottom: 35px;
        background-color: $blanc;
        border-top: 5px solid $gris-5;
        text-align: center;
        position: relative;
        z-index: 1 ;

        &:before{
            content: '';
            width: 1rem;
            height: 1rem;
            transform: rotate(45deg);
            background-color: $gris-5;
            position: absolute;
            top: -8px;
            left: 49%;
            z-index: 2;
        }


        & h2{
            margin: 0;
            font-family: $opensans;
            font-size: 1.25rem;
            color: $bleu-1;
        }

        &-footer{

            width: 100%;
            height: 80px;
            margin-bottom: -35px;

            & .btn-connexion{
                margin-top:17px;
                padding: 10px 20px;
                background-color: $bleu-5;
                border: 6px solid $gris-5;
                color: $blanc;
                border-radius: 50px;
                font-size: 1rem;

                & span{
                    margin-top: 7px;
                }

                &:hover{
                    background-color: $bleu-3;
                    color: $blanc;
                }
            }
        }
    }

    & .pictogramme-svg-user-login{
        width: 90px;
        height: 90px;
        line-height: 90px;
    }

}

//== Page de connexion 


.page-connexion{


    & .panel-title{
        text-transform: none !important;
    }

    
    &-titre{ 

        background: transparent url(#{$image-url}/trinagle-bleu-login.svg) no-repeat center bottom;

        & h1{
            background: $bleu-5;
        }

        @extend .login-box-title;

        width: 90%;
        @include media-breakpoint-up(lg){
            width: 50%;
        }


        height: 57px;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: -17px;

        &:after{
            content: none;
        }

    }

    &-box{

        border: 5px solid $gris-5;
        border-radius: 12px;
        box-shadow: none;

        @include media-breakpoint-up(lg){
            background: $blanc url(#{$image-url}/bg_login_box.svg) no-repeat center top;
        }
    }

    & h3{
        @extend .texte-courant;
        //text-align: center;
    }

    & .form-login{

        margin-top: 40px;
        margin-bottom: 5px;

        padding-left: 20px;
        padding-right: 20px;

        & input{
            background-color: $blanc !important;
        }

        & label{
            @extend .texte-courant;
            font-weight: normal;
        }

        & .btn{
            width: 100%;
        }

    } 


    .password{
        position: relative;
        
        & input{
            padding-right: 30px;
        }
        
        & .show-hide-password{
            background-color: transparent;
            border: none;
            color: $bleu-5;
            position: absolute;
            right: 3px;
            top: 7px;

            @media ( max-width: $screen-phone){
               top: 12px;
            }            
        }
    }

    & .popover{
        max-width: 360px !important;
        width: 360px !important;
    }

}