footer{
    position: relative;
    background-color: $bleu-1;
    padding-top: 20px;
    padding-bottom: 10px;
    & small{
        font-size: 0.7rem;
    }
    color: $blanc;

    & a{
        color: $blanc;

        &:hover{
            color: $blanc;
        }
    }

    & .rating{

        & .star{
            color: $blanc;
            font-size: 1.1rem;
        }
    }

    & .footer-logo{
        height: 50px;
        width: 112px;
        background: url("#{$image-url}/logo_am_footer.svg") no-repeat center;
        margin-bottom: 30px;

        @include media-breakpoint-down(sm){
            margin-right: auto;
            margin-left: auto;
        }
    }

    & .footer-copy-right{
        padding: 10px;
        margin-top: 15px;
        background-color: $bleu-1;

        & div{

            @include media-breakpoint-down(sm){
                text-align: center;
            }

        }


    }

    & .footer-contact{

        padding-left: 30px;
        @include media-breakpoint-down(sm){
            background-image: none !important;
            margin-top: 15px;
            padding-left: 0;
        }    
    }

    & ul{
        @include media-breakpoint-down(sm){
            margin-left: 25px;
            margin-top: 25px;
            margin-bottom: 25px;
        }
    }

    h3{
        margin-top: 0;
        margin-bottom: 0;

        @include media-breakpoint-down(sm){
            margin-top: 30px;
        }
    }

    li{
        margin-bottom: 15px;
    }
}

.numero-version{
    position: absolute;
    bottom: 30px;
    left: 0px;
    font-size: .63em;
    color: $gris-5;
    margin-left: 15px;
}

//== Informations utilisateur

.info-user-footer{

    padding: 10px;
    background-color: $bleu-8;
    margin: 15px -10px -7px -10px;

}