.bouton-picto{

    @include icone-cercle();

    width: 34px;
    height: 34px;
    line-height: 29px;
    font-size: 0.9rem;

    @include media-breakpoint-down(xs) {
        width: 42px;
        height: 42px;
        line-height: 39px;
        font-size: 1rem;
    }

    background-color: $gris-5;
    border: 2px solid $blanc;

    &-bleu{

        @extend .bouton-picto;

        color: $bleu-4;
        &:hover{
            color: $blanc;
            background-color: $bleu-4;
        }
    }

    &-rouge{

        @extend  .bouton-picto;
        color: $rouge-1;
        &:hover{
            color: $blanc;
            background-color: $rouge-1;
        }
    }

    &-vert{
        @extend  .bouton-picto;
        color: $vert-1;
        &:hover{
            color: $blanc;
            background-color: $vert-1;
        }
    }
}

.bouton-aide{

    @include icone-cercle();

    width: 24px;
    height: 24px;
    line-height: 22px;
    font-size: 1rem;
    vertical-align: middle;
    text-align: center;

    @include media-breakpoint-down(xs) {
        width: 32px;
        height: 32px;
        line-height: 32px;
        font-size: 1.2rem;
    }

    background-color: $blanc;

    &-bleu{

        @extend  .bouton-aide;

        color: $bleu-4;
        border: 1px solid $bleu-4;
        &:hover{
            color: $blanc;
            background-color: $bleu-4;
        }

        &.active{
            color: $blanc;
            background-color: $bleu-4;
        }

    }

    &-rouge{

        @extend  .bouton-aide;
        color: $rouge-1;
        border: 1px solid $rouge-1;
        &:hover{
            color: $blanc;
            background-color: $rouge-1;
        }
    }
    &-vert{

        @extend  .bouton-aide;
        color: $vert-2;
        border: 1px solid $vert-2;
        &:hover{
            color: $blanc;
            background-color: $vert-2;
        }
    }

    &-ocre{

        @extend  .bouton-aide;
        color: $ocre-1;
        border: 1px solid $ocre-1;
        &:hover{
            color: $blanc;
            background-color: $ocre-1;
        }
    }

}

// Bouton retour en haut de page

.btn-retour-haut{

    display: none;    
    position: absolute;
    top: -35px;
    left: 49%;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 10px;

    & a:hover,  & a:focus{
        text-decoration: none;
    }
    
    @include media-breakpoint-down(sm){
        left: 46%;
    }

}

.x-large{
    width: 44px;
    height: 44px;
    line-height: 44px;
}

//== Boutons
.btn{
    padding-left: 15px;
    padding-right: 15px;
    height: 34px;
    border-radius: 100px;
    text-align: center;
    font-size: 0.81rem;
    font-family: $opensans-semi-bold;
    text-transform: uppercase;

    min-width: 180px;

    @include media-breakpoint-down(sm){
        min-width: unset;
    }
    
    &-large {
        min-width: 320px;
    }
    
    &-big {
        width: 100%;
    }
    
    &-default{
        color: $bleu-4;
        border-color: $bleu-4;
        background-color: $blanc;
        &:hover,
            &:active,
            &:focus {
            color: $blanc;
            background-color: $bleu-3;
            border-color: $bleu-3;
        }
    }

    &-primary{
        background-color: $bleu-4;
        border-color: $bleu-4;

        &:hover,
            &:active,
            &:focus {
            color: $blanc;
            background-color: $bleu-3;
            border-color: $bleu-3;
        }

    }

    &-link{
        text-transform: none;
        color: $bleu-4;
        &:hover{
            color: $bleu-3;
        }
    }


    & .glyphicon{
        margin-right: 3px;
        margin-left: 2px;
    }

    &[disabled], &[disabled]:hover,&.disabled{
        border: 1px solid #AED7ED;
        background-color: #AED7ED;
        color: $blanc;
    }    


    &-bloc-information{

        display: inline-block;
        height: 150px;
        width: 150px;
        border: 1px solid #dbf3ff;
        margin-bottom: 15px;
        border-radius: 4px;
        padding-top: 30px;
        text-align: center;
        color: #000;
        text-decoration: none;

        &:hover {
            color: white;
            background-color: #01b9f5;
            text-decoration: none;
        }

    }

    & .panier{
        position: relative;

        & .badge{
            position: absolute;
            top: 0;
            right: -15px;
        }
    }
}

.bootstrap-select{
    & .btn{
        border-radius: 5px;
        text-transform: none;
        font-family: $opensans;
        color: $gris-1;
        border-color: $gris-4;

        @include media-breakpoint-down(sm){
            height: 42px !important;
        }

        background-color: $gris-6;

        &-default{
            &:hover{
                background-color: $gris-6;
            }
        }
    }

	&.small-select{
		
		width: 60px !important;

		.btn{
			min-width: 60px !important;
		}
	}	
}