[data-toggle="collapse"]{

    cursor: pointer;

    padding-left: 10px;
    padding-right: 10px;

    & .open-close{
        margin-left: -15px;
        color: $gris-3;
        font-size: .80rem;
        &:before { 
            
            display: inline-block;
            margin-left: .255em;
            vertical-align: .255em;
            border-top: .4em solid;
            border-right: .4em solid transparent;
            border-bottom: 0;
            border-left: .4em solid transparent;            
            
            content: ""; 
        }
    }
}

[aria-expanded="true"]{
    cursor: pointer;

    & .open-close{
        color: $bleu-5;
        
        &:before { 
            
            display: inline-block;
            margin-left: .255em;
            vertical-align: .1em;
            border-top: .4em solid transparent; 
            border-right: 0;
            border-bottom: .4em solid transparent; 
            border-left: .4em solid;            
            
            content: "";         
        }
    }
}