.card{
    margin-bottom: 5px;
}

.card-header{
    background-color: $blanc;


    & .card-title{
        font-size: 0.81rem;
    }

    & h1, h2, h3{
        margin: 0;
    }

    & .card-actions, & .card-actions-export {
        position: absolute;
        right: 10px;
        top: 10px;
    }    
}

.cards-header.active{
    background-color: $blanc !important;
}

.open-close{
    color: $bleu-5;
    font-size: .80rem;
    &:before { content: "\e250"; }

    &.active{
        &:before { content: "\e252"; }
    }
}   