// Bouton tlchargement

.telechargement-span{
    border-radius: 4px;
    padding: 1px 3px;
    color: $blanc;
}

a.btn-telechargement:hover{
    text-decoration: none;
}

.btn-telechargement{
    @include icone-cercle();
    border: 1px solid $gris-3;
    width: 34px;
    height: 34px;
    line-height: 34px;
    font-size: .6rem;
    background-color: $gris-5;
    vertical-align: middle;
    padding: 0;
    margin-right: 5px;

    &.active{
        @include telechargement-active();
    }

}

.telechargement{

    color: $gris-2;

    & span{
        @extend .telechargement-span;
    }

    &-pdf{
        @extend  .telechargement;
        & span:before{
            content: 'PDF';
        }

        & span{
            @extend .telechargement-span;
            background-color: $rouge-1;
        }

        &:hover{
            background-color: $rouge-1;
            color: $blanc;
        }

    }

    &-xls{
        @extend  .telechargement;
        & span:before{
            content: 'XLS';
        }

        & span{
            @extend .telechargement-span;
            background-color: $vert-1;
        }
        &.active{
            @include telechargement-active();
        }

        &:hover{
            background-color: $vert-1;
            color: $blanc;
        }

    }
    &-csv{
        @extend  .telechargement;
        & span:before{
            content: 'CSV';
        }
        & span{
            @extend .telechargement-span;
            background-color: $vert-1;
        }
        &.active{
            @include telechargement-active();
        }

        &:hover{
            background-color: $vert-1;
            color: $blanc;
        }

    }
    &-doc{
        @extend  .telechargement;
        & span:before{
            content: 'DOC';
        }
        & span{
            @extend .telechargement-span;
            background-color: $bleu-1;
        }
        &.active{
            @include telechargement-active();
        }

        &:hover{
            background-color: $bleu-1;
            color: $blanc;
        }

    }
    &-txt{
        @extend  .telechargement;
        & span:before{
            content: 'TXT';
        }
        & span{
            @extend .telechargement-span;
            background-color: $turquoise-1;
        }
        &.active{
            @include telechargement-active();
        }

        &:hover{
            background-color: $turquoise-1;
            color: $blanc;
        }

    }
    &-json{
        @extend  .telechargement;
        & span:before{
            content: 'JSON';
        }
        & span{
            @extend .telechargement-span;
            background-color: $jaune-1;
        }
        &.active{
            @include telechargement-active();
        }

        &:hover{
            background-color: $jaune-1;
            color: $blanc;
        }

    }

}

.chip{
    @extend .file-name;
    padding-top: 6px;
    padding-bottom: 6px;
    display: inline;
}

// Tlchargement

.pieces-jointes{

    background: url("#{$image-url}/bg-pieces-jointe.svg") no-repeat right 15px top 15px;
    
    min-height: 70px;

    padding: 10px 10px;
    border: 1px dashed $gris-3;
    border-radius: 5px;

    &:hover{
        border: 1px dashed $ocre-3;
        & p, .file-uploader{opacity: 0.4;}
    }

    &-file {
        text-align: left;
        padding: 0px 30px;
    }

    & p{
        margin: 20px 10px;
    }

    & .files-names{
        clear: both;
        margin-left: 50px;
        max-height: 350px;
        overflow: auto;
    }

}

.file-uploader{
    border: none;
    background-color: transparent;
    background-image: url("#{$image-url}/btn_pieceJointe.svg") !important;
    background-size: contain !important;

    &:hover{
        background-image: url("#{$image-url}/btn_pieceJointe_fb.svg") !important;
    }

    &.active{
        background-image: url("#{$image-url}/loader-telechargement.gif") !important;
    }
}

