    &.unstyled{
        border-radius: 0;
    }

    &-bleu{

        @extend  .pictogramme;
        color: $bleu-4;
        border: 1px solid $bleu-4;
        &:hover{
            color: $blanc;
            background-color: $bleu-4;
        }
    }
    &-rouge{
        @extend  .pictogramme;
        color: $rouge-1;
        border: 1px solid $rouge-1;
        &:hover{
            color: $blanc;
            background-color: $rouge-1;
        }
    }
    &-vert{

        @extend  .pictogramme;
        color: $vert-1;
        border: 1px solid $vert-1;
        &:hover{
            color: $blanc;
            background-color: $vert-1;
        }
    }
    &-blanc{

        @extend  .pictogramme;
        color: $blanc;
        border: 1px solid $blanc;
        background-color: transparent;

        &:hover{
            color: $blanc;
            background-color: transparent;
        }
    }


    &-svg-yeux{
        @extend  .pictogramme;
        @include before-content();
        border: none;
        background-image: url("#{$image-url}/convention_bt_suiviPC.svg");

        &:hover{
            background-image: url("#{$image-url}/convention_bt_suiviPC_fb.svg");
        }

        &.active{
            background-image: url("#{$image-url}/convention_bt_suiviPC_select.svg");
            background-color: transparent;
            height: 66px;
            width: 54px;
            border-radius: 0;
        }
    }

    &-svg-parapluie{
        @extend  .pictogramme;
        @include before-content();
        border: none;
        background-image: url("#{$image-url}/convention_bt_prevention.svg");
        &:hover{
            background-image: url("#{$image-url}/convention_bt_prevention_fb.svg");
