        &.active{
            background-image: url("#{$image-url}/convention_bt_prevention_select.svg");
            height: 66px;
            width: 54px;
            border-radius: 0;
            background-color: transparent;
        }

    }

    &-svg-cible{

        @extend  .pictogramme;
        @include before-content();
        border: none;
        background-image: url("#{$image-url}/convention_bt_efficience.svg");

        &:hover{
