
//== show-hide

.show-hide{
    cursor: pointer;
}


//== Testing

.modal-ab-testing{

    & .modal-header{
        
        background: $bleu-5 url(#{$image-url}/header-2-popin-amelipro.jpg) no-repeat bottom left;
        height: 320px;


        & ul{

            position: absolute;
            color: $blanc;
            font-size: 18px;
            margin-left: 450px;
            margin-top: 100px;

            & li {
                margin-bottom: 10px;
                padding-left: 20px;
                list-style: url(#{$image-url}/header-2-amelipro-list-tick.jpg) ;
            }
        }

        & .close{
            color: $blanc;
            opacity: 1;
            font-size: 30px;
        }

        & h4{
            color: $blanc;
            font-style: normal;
            font-size: 24px;
        }

    }
}

.remove-border{
    border: none;
}

//== nbr-totale

.nbr-totale{
    padding: 5px;
    border: 1px solid $blanc;
    font-family: $opensans-semi-bold;
    margin-top: 5px;
    display: inline-block;
    background: $gris-6;
}

.to-top{
    position: relative;
    bottom: 5px;
}

.cadre-gris{
    padding: 10px;
    border: 1px solid $gris-5;
}