//== Capsule

.capsule{
    background-color: $blanc;
    padding: 5px 10px;
    border-radius: 20px;

    .required-input{
        margin:0;
    }
}

.capsule{
    background: $gris-6;
    padding: 9px 15px;
    margin-top: 5px;
    cursor: pointer;
    display: inline-block;
    max-width: 100%;
    text-transform: uppercase;

    &-vert{
        color: $vert-1;
        &:hover, &.active{
            color: $blanc;
            background: $vert-1;
        }
    }

    &-turquoise{
        color: $turquoise-3;
        &:hover, &.active{
            color: $blanc;
            background: $turquoise-3;
        }
    }

    &-jaune{
        color: $noir; 
        &:hover, &.active{
            color: $blanc;
            background: $jaune-1;
        }
    }

    &:hover{
        text-decoration: none;
    }
}