input.datepicker{
    background: url("#{$image-url}/picto_calendrier_datepicker.svg") no-repeat;
    background-position: right;
    background-color: $gris-6;
    cursor: pointer;
    min-width: 120px;
}

.datepicker{

    text-align: left !important;

    & .prev,
    & .next,
    & .clear,
    & .datepicker-switch{
        color: $bleu-3;
    }

    & .day{
        border-radius: 0;
        border: 2px solid $blanc;
    } 

    & .today {
        background-color: $bleu-4 !important;
        border: 2px solid $blanc !important;
        color: $blanc !important;
    }


    & .datepicker-days .active,  & .datepicker-days .selected{
        background-color:  $bleu-3 !important;
        border: 2px solid $blanc !important;
		
    }

    & table tr td.disabled{
        color: $gris-3;
    }

    & table tbody{
        & tr .day:nth-last-child(-n+2){
            background-color:  $gris-6 !important;
			
				&.range{
					background-color: $bleu-7 !important;
					color: $noir !important;
				}	
				
				&.selected{
					background-color:  $bleu-3 !important;
					border: 2px solid $blanc !important;
				}

        }
    }
	
    & .range{
        background-color: $bleu-7 !important;
        color: $noir !important;
    }	

}